import React from 'react';
import { string, bool } from 'prop-types';
import cx from 'classnames';

import { Button, Input as ShInput } from '../../ui';

import './AnimatedSignupForm.scss';

export default class AnimatedSignupForm extends React.Component {
  constructor(props){
    super(props);

    this.state = {
      params: [],
      expandingInitiated: false,
      forcedExpanding: false
    };
  }

  componentDidMount(){
    const url = new URL(window.location.href);
    const params = [];

    if (!url.searchParams){
      return;
    }

    for (const key of url.searchParams.keys()){
      params.push({
        name: key,
        value: url.searchParams.get(key)
      });
    }

    this.setState({
      params
    });
  }

  componentWillReceiveProps(nextProps){
    const { expanded } = this.props;
    const { expandingInitiated } = this.state;

    if (!expanded && nextProps.expanded && !expandingInitiated){
      this.setState({
        expandingInitiated: true
      });
    }
  }

  handleSubmit = (event) => {
    event.preventDefault();

    this.setState({
      forcedExpanding: true
    });
  }

  render(){
    const { buttonText, inputPlaceholder, hasAutofocus, expanded } = this.props;
    const { expandingInitiated, forcedExpanding, params } = this.state;

    const cl = cx({
      'animatedSignupForm__input--hidden':
        expandingInitiated && !expanded && !forcedExpanding,
      'animatedSignupForm__input--visible':
        (expandingInitiated && expanded) || forcedExpanding,
      animatedSignupForm__input: true
    });

    return (
      <form
        className='animatedSignupForm__container'
        action={process.env.API_URL}
        method='post'
        onSubmit={!(expanded || forcedExpanding) && this.handleSubmit}
      >
        <div className={cl}>
          <ShInput
            placeholder={inputPlaceholder}
            name='email'
            type='email'
            required={expanded || forcedExpanding}
            small
            autoFocus={hasAutofocus}
          />
        </div>

        {params.map(item => (
          <input
            key={item.name}
            name={item.name}
            value={item.value}
            hidden
            readOnly
          />
        ))}

        <Button type='submit' small outline={!expanded}>
          {buttonText}
        </Button>
      </form>
    );
  }
}

AnimatedSignupForm.propTypes = {
  buttonText: string,
  inputPlaceholder: string,
  hasAutofocus: bool,
  expanded: bool
};

AnimatedSignupForm.defaultProps = {
  buttonText: 'Get Started for Free',
  inputPlaceholder: 'name@company.com',
  hasAutofocus: false,
  expanded: null
};



