import React from 'react';
import { func, object } from 'prop-types';
import Helmet from 'react-helmet';

import Header from './Header/Header';
import Footer from './Footer/Footer';
import MobileNav from './MobileNav/MobileNav';
import '../styles/styles.scss';
import './index.scss';

class TemplateWrapper extends React.Component {
  constructor(props){
    super(props);

    this.state = {
      isTop: true,
      intervalId: 0,
      isLanding: false,
      navVisible: false
    };
  }

  componentDidMount(){
    document.addEventListener('scroll', this.onScroll, false);

    this.setState({
      isLanding: window.location.href.includes('lp')
    });
  }

  componentWillUnmount(){
    document.removeEventListener('scroll', this.onScroll, false);
  }

  componentWillReceiveProps(){
    this.setState({
      navVisible: false
    });
  }

  onScroll = () => {
    this.setState({ isTop: window.scrollY < 100 });
  };

  showNav = value => {
    this.setState({
      navVisible: value
    });
  };

  render(){
    const { data, children, location } = this.props;
    const { isTop, isLanding, navVisible } = this.state;

    return (
      <div className='app__container'>
        <Helmet
          title='SalesHero: Sales AI'
          meta={[
            { name: 'description', content: 'AI assistant for sales reps' },
            { name: 'keywords', content: 'AI sales assistant' }
          ]}
        >
          <link
            rel='stylesheet'
            href='https://fonts.googleapis.com/icon?family=Material+Icons'
          />
          <script
            type='text/javascript'
            src={`scripts/shlogo.min.js`}
          />
        </Helmet>

        <Header
          data={data}
          isTop={isTop}
          isLanding={isLanding}
          path={location.pathname}
          navVisible={navVisible}
          showNav={this.showNav}
        />

        {navVisible && <MobileNav data={data} showNav={this.showNav} />}

        <div className="app__content">
          {children()}
        </div>

        <Footer data={data} isLanding={isLanding} />
      </div>
    );
  }
}

TemplateWrapper.propTypes = {
  children: func,
  data: object,
  location: object
};

export default TemplateWrapper;

export const query = graphql`
  query NavigationQuery {
    allMarkdownRemark {
      edges {
        node {
          frontmatter {
            scope
            title
          }
          fields {
            slug
          }
        }
      }
    }
  }
`;



