import React from 'react';
import { object, arrayOf, shape, bool } from 'prop-types';
import Link from 'gatsby-link';

import './Footer.scss';
import { Divider, Icon } from '../../ui';

const getNavItems = items =>
  items.filter(i => {
    const { scope } = i.node.frontmatter;
    return scope && scope.indexOf('FOOTER') > -1;
  });

const renderNav = data => (
  <div className='nav__items'>
    {getNavItems(data.allMarkdownRemark.edges).map((item, index) => (
      <Link
        to={item.node.fields.slug}
        key={index}
        className='footer-navigation-1'
      >
        {item.node.frontmatter.title}
      </Link>
    ))}
    <a href='https://www.linkedin.com/company/18188846/'>
      <Icon image='linkedin' />
    </a>
  </div>
);

const Footer = ({ data, isLanding }) => (
  <section className='footer__container'>
    {!isLanding && (
      <div className='footer__top'>
        <div className='footer__nav'>
          <Link to='/' className='logo__container'>
            <Icon image='logo' />
          </Link>

          {renderNav(data)}
        </div>

        <p className='footer-copytext'>
          At SalesHero, our mission is to empower sales teams through artificial
          intelligence. We are located in San Francisco, CA.
        </p>
      </div>
    )}

    {!isLanding && <Divider fullWidth />}

    <div className='footer__bottom'>
      <div className='footer__tm footer-copytext'>
        &#169; 2017 SalesHero, Inc.
      </div>

      <div className='footer__terms footer-navigation-2'>
        <Link to='/terms'>Terms of Service</Link>
        <Link to='/privacy'>Privacy policy</Link>
      </div>
    </div>
  </section>
);

Footer.propTypes = {
  data: shape({
    allMarkDownRemark: shape({
      edges: arrayOf(object)
    })
  }),
  isLanding: bool
};

export default Footer;



