import React from 'react';
import Link from 'gatsby-link';
import { arrayOf, shape, string, bool } from 'prop-types';

import './Navigation.scss';
import { AnimatedSignupForm } from '../../components/AnimatedSignupForm';

const key = node => `key-nav-item-${node.fields.slug}`;

const sortPages = (a, b) => {
  if (a.node.fields.slug < b.node.fields.slug){
    return -1;
  } else if (a.node.fields.slug > b.node.fields.slug){
    return 1;
  }
  return 0;
};

const Navigation = ({ pages, isTop, path }) => (
  <ul className='nav__container'>
    {pages.sort(sortPages).map(({ node }) => (
      <li
        key={key(node)}
        className={
          path.includes(node.fields.slug) ? 'nav__item--active' : 'nav__item'
        }
      >
        <Link to={node.fields.slug} className='main-navigation'>
          {node.frontmatter.title}
        </Link>
      </li>
    ))}

    <li>
      <AnimatedSignupForm expanded={!isTop} />
    </li>
  </ul>
);

Navigation.propTypes = {
  pages: arrayOf(
    shape({
      node: shape({
        fields: shape({
          slug: string
        })
      })
    })
  ),
  isTop: bool,
  path: string
};

export default Navigation;



